  //1
  d1c1Series: { name: [], data: [] },
  d1c2Series: { name: [], data: [] },
  d1c3Series: { name: [], data: [] },
  d1c4Series: { name: [], data: [] },
  //2
  d2c1Series: { name: [], data: [] },
